import axios from 'axios'

export default {
  name: 'list',
  data () {
    return {
      projectList: Array
    }
  },
  methods: {
    getProjectList () {
      // const level = localStorage.getItem('level');
      axios.get(this.url + '/project/all')
        .then(this.getProjectListSucc)
    },
    getProjectListSucc (res) {
      res = res.data
      if (res === 'ProjectsFindByParticipants No Project Record') this.$router.push('/noProject')
      else if (res) {
        this.projectList = res
      }
    }
  },
  mounted () {
    this.getProjectList()
  }
}



