import ProjectHeader from './components/header'
import ProjectList from './components/list'

export default {
  name: 'index',
  components: {
    ProjectHeader,
    ProjectList
  },
  methods: {
    judge () {
      if (!localStorage.getItem('id')) {
        this.$router.push('/')
        alert('您还没有绑定用户哦！')
      }
    }
  },
  created () {
    this.judge()
  }
}



